import { createRouter, createWebHistory } from "vue-router"
import regNumber from "@/pages/regNumber.vue"
import regInfo from "@/pages/regInfo"

const routes = [

  {
    path: '/', 
    name: 'regNumber',
    component: regNumber
  },

  {
    path: '/regInfo',
    name: 'regInfo',
    component: regInfo
  },
  
]

const router = createRouter({
  routes,
  history: createWebHistory(),
})

export default router; 

